.before-after {
    height: 405px !important;
    width: 400px;
    border-radius: 30px;
    position: relative;
    img {
        position: absolute;
        top: -9999px;
        bottom: -9999px;
        right: -9999px;
        left: -9999px;
        margin: auto; }
    &:before {
        content: '';
        border-radius: 30px;
        position: absolute;
        z-index: 100;
        border: 2px solid #FFFFFF;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        top: 5px;
        left: 5px; } }
.slide-ps {
    font-size: 14px;
    line-height: 133.02%;
    color: #FA508F;
    font-family: 'Proxima Nova Bold'; }

.twentytwenty-horizontal .twentytwenty-handle {
    &:before,
    &:after {
        width: 2px;
        transform: translateX(1.5px); } }

.twentytwenty-handle {
    top: auto;
    bottom: 50px;
    background: #FA6398;
    box-shadow: 5px -3px 15px rgba(250, 99, 152, 0.55), inset 9px -8px 20px rgba(249, 64, 126, 0.25);
    border: none;
    z-index: 110; }

#after-before {
    position: relative; }

.ptl7 {
    position: absolute;
    top: -110px;
    left: 0; }
