#footer {
    background-image: url('../img/dotted-bg.svg');
    background-repeat: repeat;
    padding-top: 80px;
    padding-bottom: 110px;
    position: relative; }

.footer-container {
    position: relative;
    display: flex;
    justify-content: flex-end; }

.footer-content {
    padding: 60px 55px 55px 70px;
    background: #FFFFFF;
    border-radius: 36px;
    color: #272727;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    &:before {
        content: '';
        position: absolute;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        top: 10px;
        left: 10px;
        border: 1px solid #F6F6F6;
        border-radius: 36px;
        pointer-events: none; } }

.footer-title {
    text-align: left;
    font-size: 22px;
    margin-bottom: 35px;
    line-height: 26px; }

.footer-subtitle {
    font-family: 'Proxima Nova Bold'; }

.footer-txt {
    margin-bottom: 20px; }

.footer-messanger {
    margin-top: 30px;
    display: inline-flex;
    width: 35px;
    height: 35px;
    border: 1px solid #FA5894;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    svg path {
        transition: .3s; } }

.footer-messanger:hover {
    background: #FA5894;
    svg path {
        transition: .3s;
        fill: #fff; } }

.footer-map-wrap {
    background: #C4C4C4;
    border-radius: 26px;
    width: 800px;
    height: 610px; }

.ptl13 {
    position: absolute;
    bottom: 5px;
    left: 55px; }

.footer-bg {
    position: absolute;
    top: -205px;
    left: -120px; }
