#work-place {
    padding-top: 25px;
    .section-title {
        margin-bottom: 50px; } }

.work-imgs {
    display: flex;
    margin-bottom: 130px; }

.work-img {
    width: 25%;
    height: 240px;
    position: relative;
    overflow: hidden;
    transition: .3s;
    cursor: pointer;
    img {
        position: absolute;
        top: -9999px;
        bottom: -9999px;
        right: -9999px;
        left: -9999px;
        margin: auto;
        min-width: 100%;
        height: auto; }
    &:hover {
        transition: .3s;
        box-shadow: 5px -5px 25px rgba(251, 87, 147, 0.5); }
    &:before {
        content: '';
        position: absolute;
        background-image: url('../img/wrok-space/plus.svg');
        background-position: center center;
        background-repeat: no-repeat;
        background-color: rgba(251, 87, 147, 0.7);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 10;
        opacity: 0;
        transition: .3s; }
    &:hover:before {
        transition: .3s;
        opacity: 1; } }

