#certificates {
    padding: 220px 0 70px;
    position: relative;
    .right-triangle {
        top: 140px; } }

.cert-container {
    display: flex;
    position: relative; }

.cert-sldr-container {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center; }

.cert-slider {
    width: 355px;
    height: 255px; }

.cert-content {
    width: 50%;
    h2, p {
        text-align: left; } }


.cert-wrap {
    width: 355px;
    height: 255px;
    overflow: hidden;
    position: relative; }

.cert-img {
    position: absolute;
    top: -9999px;
    bottom: -9999px;
    left: -9999px;
    right: -9999px;
    margin: auto; }

.cert-border {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1; }

.mkup {
    position: absolute;
    right: 100px;
    top: 0; }

.cert-bg-elipse {
    top: -20px;
    left: 65px;
    bottom: auto;
    width: 205px;
    height: 205px;
    img {
        filter: brightness(1.3); }
    &:before {
        width: calc(100% + 14px);
        height: calc(100% + 14px);
        top: -7px;
        left: -7px; } }


.legs {
    position: absolute;
    left: 0;
    bottom: 0; }
