#reviews {
    position: relative;
    margin-top: 170px; }

.reviews-container {
    width: 645px;
    height: 645px;
    border-radius: 50%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #F9F9F9 100%);
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; }

.reviews-container {
    position: relative; }

.phone-shadow,
.phone-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 0; }

.phone-img {
    z-index: 10; }

.phone-slider {
    position: relative;
    z-index: 5;
    width: 210px;
    height: 445px; }

.ptl8 {
    position: absolute;
    left: 0;
    top: -100px; }

.tools {
    position: absolute;
    left: 40px;
    top: 130px; }

.ptl9 {
    position: absolute;
    left: 51%;
    top: 70px;
    z-index: 300;
    pointer-events: none; }

.l3 {
    position: absolute;
    left: 0;
    top: 200px; }
