.sliders {
    position: relative; }

.sldr-bg-elipse {
    position: absolute;
    bottom: -75px;
    right: 10px;
    img {
        height: 100%;
        width: 100%; }
    &:before {
        content: '';
        position: absolute;
        width: calc(100% + 10px);
        height: calc(100% + 10px);
        top: -5px;
        left: -5px;
        border: 1px solid #EED7DF;
        border-radius: 50%; } }

.sldr-bg-lines {
    position: absolute;
    top: -80px;
    left: 63px; }



.slider {
    margin-top: 70px;
    position: relative;

    .glide__slide {
        text-align: center; } }

.slide-container {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    text-align: left; }

.slide-content {
    padding: 70px 30px 45px 70px;
    background: #FFFFFF;
    border-radius: 27px;
    position: relative;
    margin-right: 25px;
    max-width: 640px;
    &:before {
        content: '';
        position: absolute;
        top: 10px;
        left: 10px;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        border: 1px solid #F6F6F6;
        border-radius: 27px; } }

.slide-title {
    font-family: 'Berlingske Serif SemiBold';
    font-size: 24px;
    color: #272727;
    margin-bottom: 45px; }

.slide-text {
    font-size: 18px;
    line-height: 133.02%;
    color: #747474;
    margin-bottom: 30px; }

.glide__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 90px;
    height: 90px;
    background: #FA6398;
    box-shadow: 5px -3px 15px rgba(250, 99, 152, 0.55), inset 9px -8px 20px rgba(249, 64, 126, 0.25);
    border: none;
    border-radius: 50%;
    &:focus {
        outline: 0; } }
.glide__arrow--left {
    left: -10px;
    img {
        transform: translateX(-3px); } }
.glide__arrow--right {
    right: -20px;
    img {
        transform: translateX(3px) rotate(180deg); } }


.glide__bullets {
    margin-top: 45px;
    text-align: center; }

.glide__bullet {
    background: #FA6398;
    border: none;
    border-radius: 50%;
    padding: 3px;
    margin: 0 7px;
    position: relative;
    transition: .3s;
    &:before {
        transition: .3s;
        content: '';
        position: absolute;
        width: calc(100% + 10px );
        height: calc(100% + 10px);
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0);
        border: 1px solid #FA6398;
        border-radius: 50%; }
    &:focus {
        outline: 0; } }

.glide__bullet--active:before {
    transform: translate(-50%, -50%) scale(1); }


.review-img-wrap {
    overflow: hidden;
    width: 210px;
    height: 445px;
    position: relative;
    img {
        position: absolute;
        top: -9999px;
        bottom: -9999px;
        right: -9999px;
        left: -9999px;
        margin: auto; } }

.phone-slider,
.cert-slider {
    .glide__arrow {
        width: 55px;
        height: 55px;
        &:before {
            content: '';
            position: absolute;
            border-radius: 50%;
            border: 0.5px solid #FFFFFF;
            width: calc(100% - 4px);
            height: calc(100% - 4px);
            top: 2px;
            left: 2px; }
        img {
            width: 12px; } } }

.phone-slider {
    .glide__arrow--left {
        left: -100px; }
    .glide__arrow--right {
        right: -100px; } }

.phone-sldr-counter,
.cert-sldr-counter {
    position: absolute;
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'Berlingske Serif SemiBold';
    font-style: italic;
    line-height: 116.02%;
    letter-spacing: -0.075em;
    color: #272727;
    font-size: 36px; }

.current-sldr {
    color: #FA6398; }

.slash-sldr {
    margin: 0 3px; }

.all-sldr {
    font-size: 18px; }


.cert-slider {
    position: relative; }


.cert-slider {
    .glide__arrow--left {
        left: -65px; }
    .glide__arrow--right {
        right: -65px; } }

.cert-sldr-counter {
    bottom: -65px; }
