#capture-form {
    padding-top: 85px;
    padding-bottom: 20px;
    margin: 100px 0;
    background: #FCFCFC;
    overflow: hidden; }

.capture-container {
    position: relative;
    display: flex;
    justify-content: flex-end; }

.capture-img {
    position: absolute;
    top: 50%;
    left: -85px;
    transform: translateY(-58%); }

.capture-txt {
    line-height: 122.02%;
    color: #747474;
    margin: 30px 0 60px; }

.capture-form input {
    width: 270px;
    &[type="text"] {
        margin-right: 25px; } }

.capture-desc {
    font-size: 12px;
    line-height: 122.02%;
    color: #747474;
    display: flex;
    margin-top: 30px;
    margin-bottom: 5px;
    p {
        margin-top: 7px;
        margin-left: 10px; } }
