#stock {
    padding-top: 180px;
    position: relative;
    overflow: hidden;
    .container {
        position: relative;
        z-index: 15; } }

.stocks {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    max-width: 1070px;
    width: 100%;
    height: 100%;
    margin: 60px auto 0; }

.stock-itm {
    width: 33.333%;
    padding: 0 40px;
    margin-bottom: 60px; }

.stock-itm-content {
    border-radius: 29px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 118px 32px 42px;
    text-align: center;
    position: relative;
    background-size: cover;
    background-position: center center;
    &:before {
        content: '';
        position: absolute;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        left: 5px;
        top: 5px;
        border: 1px solid #EFEFEF;
        border-radius: 29px;
        pointer-events: none; } }
.stock-itm-txt {
    font-family: 'Berlingske Serif SemiBold';
    font-size: 24px;
    line-height: 126.52%;
    color: #272727;
    margin-bottom: 60px; }

.fz20 {
    font-size: 20px; }

.stock-itm-btn {
    padding: 22px 40px;
    border: 1px solid #FB5793;
    border-radius: 32.5px;
    background: transparent;
    font-family: 'Proxima Nova Bold';
    font-size: 16px;
    line-height: 126.52%;
    text-align: center;
    color: #FB5793;
    cursor: pointer;
    &:focus {
        outline: none; } }


.left-triangle {
    position: absolute;
    top: 90px;
    left: 0;
    z-index: 5; }

.ptl11 {
    position: absolute;
    top: 320px;
    left: 195px;
    z-index: 0; }

.ptl12 {
    position: absolute;
    top: 440px;
    right: -160px;
    z-index: 0; }

.l4 {
    top: 510px;
    right: 0;
    z-index: 5; }

.right-stock-cream {
    position: absolute;
    right: 30px;
    top: 745px;
    z-index: 10; }

.title-cream {
    position: absolute;
    top: -70px;
    right: 160px; }

.stock-bg-elipse {
    bottom: auto;
    top: -140px;
    right: 145px;
    height: 250px;
    width: 250px;
    &:before {
        display: none; } }
