#features {
    background-image: url('../img/dotted-bg.svg');
    background-repeat: repeat;
    padding-top: 75px;
    padding-bottom: 100px;
    position: relative; }

.fetures-container {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 120px; }

.feature {
    display: flex;
    align-items: flex-start;
    position: relative; }

.ftre-nmbr {
    font-family: 'Berlingske Serif SemiBold';
    font-style: italic;
    font-size: 78px;
    line-height: 146.8%;
    letter-spacing: 0.017em;
    color: #F0F0F0;
    position: absolute;
    top: -65px;
    left: 15px; }

.ftre-img {
    margin-top: 50px; }

.ftre-txt {
    margin-left: 15px;
    max-width: 370px;
    z-index: 10; }

.ftre-title {
    font-family: 'Berlingske Serif SemiBold';
    font-size: 20px;
    line-height: 24px;
    color: #272727;
    margin-bottom: 45px; }

.ftre-desc {
    line-height: 27px;
    color: #747474; }

.feature:first-of-type .ftre-txt {
    max-width: 270px; }

.feature:nth-of-type(-n+2) {
    margin-bottom: 245px; }

.top-cream {
    position: absolute;
    top: -165px;
    right: -35px; }

.center-cream {
    position: absolute;
    left: 50%;
    transform: translate(-55%, 10%);
    z-index: 5; }

.ftre-ellipse-bg {
    position: absolute;
    left: 50%;
    transform: translate(-55%, 13%);
    z-index: 0; }

.ptl6 {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0; }

.left-cream {
    left: 0;
    bottom: 40px;
    position: absolute; }

.right-triangle {
    position: absolute;
    right: 0;
    top: 40px; }

.fetures-imgs-container {
    position: relative;
    margin-top: 140px; }
