.mCS-custom-theme.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar{ 
    background: #FA5290;
    box-shadow: 4px -2px 15px rgba(250, 82, 144, 0.2);
    border-radius: 10px;
    width: 20px;
    height: 62px;
}

#mCSB_3_scrollbar_vertical{
    left: -20px;
}

#mCSB_3_container{
    padding-right: 0;
    padding-left: 50px;
}

.mCS-custom-theme.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar:after{
    content: '';
    position: absolute;
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    border-radius: 10px;
    border: 0.6px solid #FFEBF4;
    top: 2px;
    left: 2px;
}
.mCS-custom-theme .mCSB_container{
    padding-right: 30px;
}

.mCS-custom-theme.mCSB_scrollTools .mCSB_draggerRail{ 
    background-color: #F2F2F2; 
    width: 1px;
}

.mCS-custom-theme .mCSB_scrollTools {
    width: 60px;

}

.mCS-custom-theme .mCSB_dragger{
    max-height: 62px !important;
}