#feedback {
    margin-top: 60px;
    padding: 100px 0 60px;
    background: #FAFAFA;
    position: relative; }

.feedback-container {
    position: relative; }

.feedback-cosmetics {
    position: absolute;
    right: 100px;
    top: 0; }

.feedback-bodegon {
    position: absolute;
    right: 0;
    top: 220px; }

.ptl10 {
    position: absolute;
    top: 50px;
    left: 0; }


.feedback-content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 5;
    .dotted-btn {
        margin-top: -100px; } }

.feedback-bazilik {
    margin-left: 200px; }

.fbk-messanger {
    display: block;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #FA6398;
    box-shadow: inset 0px 4px 25px #EA457C;
    border-radius: 50%;
    z-index: 1;
    p {
        font-size: 11px;
        line-height: 13px;
        color: #FFFFFF;
        font-family: 'Proxima Nova Regular';
        font-style: italic; }
    &:before {
        content: '';
        position: absolute;
        width: calc(100% - 13px);
        height: calc(100% - 13px);
        top: 6.5px;
        left: 6.5px;
        border-radius: 50%;
        border: 0.5px solid #EED7DF;
        box-sizing: border-box; } }

.light-msngr {
    background: #FFE5EE;
    box-shadow: none;
    z-index: 0;
    p {
        color: #FA6297; }
    &:before {
        border-color: #FEC0D5; } }

.fbk-messanger:nth-of-type(1) {
    top: 85px;
    left: 340px;
    height: 160px;
    width: 160px; }

.fbk-messanger:nth-of-type(2) {
    top: 115px;
    left: 485px;
    height: 100px;
    width: 100px; }

.fbk-messanger:nth-of-type(3) {
    top: 125px;
    left: 560px;
    height: 140px;
    width: 140px; }

.fbk-messanger:nth-of-type(4) {
    top: 80px;
    left: 660px;
    height: 220px;
    width: 220px; }
